import { Component, OnInit, Input, ChangeDetectorRef } from '@angular/core';
import { AccountService } from 'src/app/account/account.service';
import { CommentService } from './comment.service';
import { LoginService } from 'src/app/login/login.service';

@Component({
  selector: 'app-comment',
  templateUrl: './comment.component.html',
  styleUrls: ['./comment.component.scss']
})
export class CommentComponent implements OnInit {
  @Input() blog_id:any
  @Input() blog:any
  @Input() position:any=''
  commentor_list:any={}
  commentors_details:any
  constructor(private LS:LoginService, private CS:CommentService, private cdr:ChangeDetectorRef) {
    this.LS.addChild(this);
    this.CS.addChild(this);
   }
  comments:any=null
  ngOnInit() {
    //console.log(this.comments);
    this.refresh();
  }
  hidecomment(){
    document.getElementById("comment123").style.display="none";
    console.log("comment hidden");
  }
  showcomment(){
    document.getElementById("comment123").style.display="block";
    console.log("comment shown");
  }
  refresh(){
    this.cdr.detectChanges();
    this.CS.get_comments(this.blog_id).subscribe((data:any)=>{
      //console.log(data)
      this.comments={}
      for(let x of data){
        //console.log(x.comment.message, x.user.info)
        try{
          x.user.info = JSON.parse(x.user.info)
        } catch(e){
          alert("Error in parsing json at comment at line 28 :" + e)
        }
        this.comments[x.comment.url]=x;
      }
    })
  }
}
