import { Injectable } from '@angular/core';
import { LoginService } from 'src/app/login/login.service';
import { HttpParams } from '@angular/common/http';

@Injectable({
  providedIn: 'root'
})
export class CommentService {

  constructor(private LS:LoginService) { }

  comment(message, blog_url, parent_comment_url){
    this.LS.http.post(
      this.LS.serverurl+'blogs/comments/',
      new HttpParams()
        .set('message', message)
        .set('blog', blog_url)
        .set('parent', parent_comment_url)
        ,
        {
          headers:this.LS.getHeaders()
        }
    ).subscribe(data=>{
      this.refresh();
    })
  }

  get_comments(blog_id){
    return this.LS.http.get(this.LS.serverurl+'blogs/comments/?blog='+blog_id, {
      headers:this.LS.getHeaders()
    })
  }


   
  child_elements=[];
  refresh(){
    for(let x in this.child_elements){
      this.child_elements[x].refresh();
    }
  }
  addChild(x:any){
    if(this.child_elements.indexOf(x)==-1){
      this.child_elements.push(x);
    }
  }
}
